<template>
  <li class="nav-item" v-if="item.category == 'array'">
    <a :id="`${item.idName}-link`" class="nav-link collapsed" :data-bs-target="`#${item.idName}`" data-bs-toggle="collapse" href="javascript:void(0)">
      <div class="vrline"></div>
      <span>{{item.cateText}}</span><i class="bi bi-chevron-down ms-auto"></i>
    </a>
    <ul :id="`${item.idName}`" class="nav-content collapse " data-bs-parent="#sidebar-nav">
        <li v-for="(items, index) in item.menu"
            :key="index"><router-link :to="`${items.link}`" @click="closeSidebar"><i class="bi bi-caret-right-fill"></i> {{items.text}}</router-link></li>
    </ul>
  </li>
  <li class="nav-item" v-if="item.category == 'static'">
    <router-link :to="`${item.cateLink}`" class="nav-link collapsed" @click="closeSidebar">
        <div class="vrline"></div>
        <span>{{item.cateText}}</span>
    </router-link>
  </li>
</template>

<script>
export default {
  name: 'item',
  components: {},
  props: {
    item: {
      type: Object
    }
  },
  data() {
    return {}
  },
  methods: {
    closeSidebar(){
      if(window.innerWidth < 1199){
        document.getElementById('menu').classList.remove("open");
        document.body.classList.remove("toggle-sidebar");
      }
    }
  },
  created() {},
  mounted() {}
};
</script>
