<template>
  <div class="headbox">
    <div class="logobox">
      <div class="img">
        <router-link to="/">
          <img src="@/assets/logo.svg" alt="logo" />
        </router-link>
      </div>
      <div class="menubtnbox" @click="toggleMenu">
        <div id="menu" class="icon">
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
    <userInfo />
  </div>
</template>

<script>
import { isAndroid, isIOS, isBWPlusApp } from '@/utils/browser';
import userInfo from '@/components/Admin/Header/userInfo'

export default {
  name: 'Header',
  components: {
    userInfo
  },
  data() {
    return {
      isShowMenu:false
    }
  },
  methods: {
    toggleMenu(){
      document.getElementById('menu').classList.toggle("open");
      document.body.classList.toggle("toggle-sidebar");
    },
    menuShow(){
      let vm = this;
      if(isAndroid || isIOS || isBWPlusApp){
        vm.isShowMenu = true;
      }
    }
  },
  created() {
    let vm = this;
    vm.menuShow();
  },
  mounted() {},
  updated() {},
  beforeUnmount() {},
  unmounted(){}
};
</script>
