<template>
    <aside id="sidebar" class="sidebar">
        <ul class="sidebar-nav" id="sidebar-nav">
            <sidebarItem v-for="(items, index) in menuList" :key="index" :item="items" />
            <li class="nav-item sidebarlogout">
                <a href="javascript:void(0)" class="nav-link collapsed" @click="logoutFunc">
                    <div class="vrline"></div>
                    <span>登出</span>
                </a>
            </li>
        </ul>
    </aside>
</template>

<script>
import sidebarItem from '@/components/Admin/Sidebar/item'

export default {
    name: 'Sidebar',
    components: {
        sidebarItem
    },
    data() {
        return {
            menuList:[{
                cateText:'報名列表',
                category:'static',
                cateLink:'/RegistrList',
                idName:'',
                menu:[]
            }]
        }
    },
    methods: {
        openCourse(){
            let tmpCourseTag = document.getElementById('course');
            let tmpCourseLinkTab = document.getElementById('course-link');
            if(location.href.indexOf('List') > -1 || location.href.indexOf('Add') > -1){
                tmpCourseLinkTab.classList.remove('collapsed');
                tmpCourseTag.classList.add('show');
            }else{
                tmpCourseLinkTab.classList.add('collapsed');
                tmpCourseTag.classList.remove('show');
            }
        },
        logoutFunc(){
            location.reload();
        }
    },
    created() {},
    mounted() {},
    unmounted(){}
};
</script>
